.person-card-container {
    margin-top: 5rem;
    margin-bottom: 5rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    max-width: 81%;
}

.person-card {
    border-color: crimson;
    position: relative;
    margin: 1rem;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.5s ease-in-out;
    width: 20rem;
    height: 26rem;
    max-width: 20rem;
    max-height: 26rem;
    border-radius: 10px;
}

.person-card:hover {
    transform: scale(1.05);
}

.person-card img {
    max-width: 100%;
    height: auto;
    border-radius: 10px;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.7);
    opacity: 0;
    transition: opacity 0.5s ease-in-out;
    border-radius: 10px;
}

.info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: white;
    font-size: 1.2rem;
    margin-top: 20rem;
}

.info p {
    margin-left: 2rem;
    font-size: 1.5rem;
}

.person-card:hover .overlay {
    opacity: 1;
}

.overlay img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    transition: transform 0.5s ease-in-out;
    margin-right: 4rem;
}

.overlay img:hover {
    transform: scale(1.2);
}